<template>
  <div class="app-container">
    <div class="cell">
      <div>
        <span>2020年9月10日</span>
      </div>
      <div>
        <span>+</span>
        <span class="bold">56</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {},
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
.app-container {
  background-color: #fff;
  min-height: 100vh;
  padding: 0 15px;
  .cell {
    display: flex;
    justify-content: space-between;
    padding: 15px 0;
    border-bottom: 1px solid #dbdbdb;
    font-size: 14px;
    color: #333333;
    .bold {
      color: #000;
    }
  }
}
</style>


